@import url(https://fonts.googleapis.com/css2?family=Antonio:wght@600&family=Playfair+Display:ital@0;1&display=swap);
body {
  margin: 0;
  font-family: 'Playfair Display', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #293241;
  font-family: 'Playfair Display', serif;
  background-color: #e0fbfc;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.copyrightSection {
  text-align: center;
  position:absolute;
  left:0;
  bottom:0;
  right:0;
  margin-bottom: 2em;
}

a {
  color: #ee6c4d;
}

.siteTitle {
  font-family: 'Antonio', Helvetica, sans-serif;
  text-align: center;
  color: #ee6c4d !important;
  font-size: 3em;
}

.siteSubtitle {
  text-align: center;
  margin-top: 2%;
}
.jobContainer {
    margin-top: 10%;
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-justify-content: center;
            justify-content: center;
}

.jobRow {
    font-family: 'Playfair Display', serif;
    outline-style: solid;
    outline-color: #3d5a80;
    background-color: #e0fbfc;
    outline-width: 1px;
    margin: 10px;
    width: 250px;
    height: 200px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.jobDetails {
    margin: none;
}

/* company */
.jobRow h1 {
    font-weight: 600;
    color: #293241;
    font-size: 1.5em;
    line-height: 0.5em;
}
/* title */
.jobRow h2 {
    font-weight: 300;
    font-size: 1em;
}
/* date */
.jobRow h3 {
    color: #3d5a80;
    font-weight: 300;
    font-size: 1em;
}

.infoBox {
    outline-style: groove;
    outline-color: #3d5a80;
    background-color: #e0fbfc;
    outline-width: 1px;
    width: 250px;
    padding: 10px;
    margin: 10px;
    margin-top: 10%;
    font-family: 'Playfair Display', serif;
}

.infoBox:hover {
    background-color: #98c1d9;
    cursor:pointer;
}

.subtitleText {
    color: #3d5a80;
    text-align: center;
}

.titleText {
    text-align: center;
    font-size: 1.5em;
}

.bodyFirstLetter {
    float: left;
    font-size: 4em;
    padding: 0.05em;
    line-height: 0.6em;
}

.bodyFirstSentence {
    color: #ee6c4d;
}

.linkText {
    color: #ee6c4d;
    text-align: right;
}

.arrowRight {
    width: 0; 
    height: 0;
    border-top: 0.4em solid transparent;
    border-bottom: 0.4em solid transparent;
    border-left: 0.4em solid #ee6c4d;
    line-height: 0;
    margin-right: 5px;
    display: inline-block;
}

ul {
    list-style:     none;
    float: right;
    clear: both;
    margin:         0.75em 0;
    padding:        0 1em;
}

li:before {
    content:        "";
    border-color:   transparent #ee6c4d;
    border-style:   solid;
    border-width:   0.35em 0 0.35em 0.45em;
    display:        block;
    height:         0;
    left:           -1em;
    position:       relative;
    top:            0.9em;
    width:          0;
}
.boxesSection {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 100%;
    -webkit-justify-content: center;
            justify-content: center;
}
